@import url(https://use.fontawesome.com/releases/v5.0.9/css/all.css);


.container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.canvas {
    border:2px solid black;
}
.tools {
    border:2px solid black;
}
ul {
    padding-left: 0px;
}
h4 {
    padding-left: 4em;
}
.pen_color ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-left: 0px;
}
.pen_color ul li{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 13px;
    height: 13px;
    border: 3px #fff solid;
    margin: 8px;
    cursor: pointer;
}
.pen_width ul {
    padding: 0 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.pen_width ul li{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 13px;
    height: 13px;
    margin: 8px;
    cursor: pointer;
}
.small {
    font-size: 12px;
}
.middle {
    font-size: 14px;
}
.big {
    font-size: 16px;
}
.canvas_control_btn{
    padding: 0 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    cursor: pointer;
}

